
import {request} from './request'

// 
export function getUserAdminDetail(){
  let cookies = localStorage.getItem('userInfo')?localStorage.getItem('userInfo'):''
  let encode = encodeURIComponent(cookies)
  return request({
    url:'/user/account',
    params:{
      cookie:encode
    }
  })
}
