// 
export function getUserPlayList(uid,offset){
  let cookies = localStorage.getItem('userInfo')?localStorage.getItem('userInfo'):''
  let encode = encodeURIComponent(cookies)
  if(offset){
    return request({
      url:'/user/playlist',
      params:{
        uid,
        cookie:encode,
        offset
      }
    })
  }else{
    return request({
      url:'/user/playlist',
      params:{
        uid,
        cookie:encode,
      }
    })
  }
}