// 
import {request} from './request'
// 
export function searchSugGest(keywords){
  return request({
    url:'/search/suggest',
    params:{
      keywords
    }
  })
}

// 
export function searchList(keywords,type,offset){
  if(offset){
    return request({
      url:'/search',
      params:{
        keywords,
        type,
        limit:30,
        offset
      }
    })
  }else{
    return request({
      url:'/search',
      params:{
        keywords,
        type,
        limit:30,
      }
    })
  }

}