export const dt = {
  computed: {
    day() {
      let date = new Date();
      let d = date.getDay();
      return {
        0: "",
        1: "",
        2: "",
        3: "",
        4: "",
        5: "",
        6: "",
      }[d];
    },
    date() {
      let date = new Date();
      let d = date.getDate();
      return d;
    },
  },
};
// 
export const songPlay = {
  methods: {
    bofang(id, noCopyrightRcmd) {
      // 
      if (noCopyrightRcmd) {
        this.showNocopyTips();
        return;
      } else {
        getSongDetail(id).then((b) => {
          let flag = true;
          let song = JSON.parse(
            localStorage.getItem("song") ? localStorage.getItem("song") : "[]"
          );
          for (let i = 0; i < song.length; i++) {
            if (song[i].id === id) {
              flag = false;
              break;
            }
          }
          // flag true 
          if (flag) {
            // 
            song.push(b.data.songs[0]);
            localStorage.setItem("song", JSON.stringify(song));
            //  index 
            this.$store.commit("LASTINDEX");
            if (this.$store.state.flag) {
              this.$store.commit("ISFLAG");
            }
            this.$bus.$emit("block");
            this.$store.dispatch("songUrl");
            this.$bus.$emit("update");
          } else {
            // 
            let i = song.findIndex((item) => {
              return item.id === id;
            });
            // index
            this.$store.commit("CACHEINDEX", i);
            if (this.$store.state.flag) {
              this.$store.commit("ISFLAG");
            }
            this.$bus.$emit("block");
            this.$store.dispatch("songUrl");
            this.$bus.$emit("update");
          }
        });
      }
    },
  },
};
// 
export const toPlay = {
  methods: {
    // 
    toPlay() {
      let songList = this.songList.filter((item) => {
        return item.noCopyrightRcmd === null;
      });
      //  songList 
      localStorage.setItem("song", JSON.stringify(songList));
      // 
      this.$bus.$emit("update");
      // true 
      if (this.$store.state.flag) {
        this.$store.commit("ISFLAG");
      }
      // 
      this.$bus.$emit("block");
      // index
      this.$store.commit("RESETINDEX");
      // 
      this.$store.dispatch("songUrl");
      // 
      this.$bus.$emit("load");
    },
  },
};
// 
export const albumPlay = {
  methods: {
    handleToBoFang(id) {
      this.$store.commit("RESETINDEX");
      this.$bus.$emit("block");
      if (this.$store.state.flag) {
        this.$bus.$emit("load");
        this.$store.commit("ISFLAG");
      }
      getAlbumSongs(id).then((v) => {
        // Vuex
        this.$store.commit("CUNSONGSI", v.data.songs);
        // 
        this.$bus.$emit("update");
        // 
        this.$store.dispatch("songUrl");
      });
    },
  },
};
// 
export const addSong = {
  methods: {
    addList(id, item) {
      if (item.noCopyrightRcmd && !item.noCopyrightRcmd.songId)
        return this.showNocopyTips();
      // 
      let songList = this.songList.find((item) => {
        return item.id == id;
      });
      // 
      let songs = JSON.parse(localStorage.getItem("song"));
      // 
      songs = songs || [];
      // 
      let simiList = songs.find((item) => {
        return songList.id === item.id;
      });
      // 
      if (simiList !== undefined) {
        this.$bus.$emit("block");
        this.$bus.$emit("showAddTips");
      } else {
        // 
        songs.push(songList);
        localStorage.setItem("song", JSON.stringify(songs));
        this.$bus.$emit("block");
        this.$bus.$emit("update");
        this.$bus.$emit("showAddTips");
      }
    },
  },
};
