import { request } from "./request";
//  key
export function getQrCodeKey(){
  return request({
    url:'/login/qr/key'
  })
}
// 
export function getQrCode(key){
  return request({
    url:`/login/qr/create`,
    params:{
      key,
      qrimg:'qrimg'
    }
  })
}

// 
export function getQrCheck(key,time){
  return request({
    url:`/login/qr/check`,
    params:{
      key,
      timerstamp:time
    }
  })
}