export function getPlaylistHighquality({cat,offset,order}){
  if(cat){
   return request({
      url:'/top/playlist',
      params:{
         limit:30,
         cat,
         offset,
         order
      }
   })
  }else{
   return request({
      url:'/top/playlist',
      params:{
         limit:30,
         offset,
         order
      }
   })
  }

}

