export function getTopArtist(offset){
   if(offset){
      return request({
         url:'/top/artists',
         params:{
            limit:30,
            offset:(offset-1)*30
         }
      })
   }else{
      return request({
         url:'/top/artists',
         params:{
            limit:30,
         }
      })
   }
}
// ()
export function getArtistList(type,area=-1,initial,offset){
   if(offset){
      return request({
         url:'/artist/list',
         params:{
            type,
            area,
            initial,
            offset:(offset-1)*30
         }
      })
   }else if(initial){
      return request({
         url:'/artist/list',
         params:{
            type,
            area,
            initial,
         }
      })
   }else{
      return request({
         url:'/artist/list',
         params:{
            type,
            area,
         }

      })
   }
}

