// 
export function sendComment({t,type,id,content}){
   let cookies = localStorage.getItem('userInfo')?localStorage.getItem('userInfo'):''
   let encode = encodeURIComponent(cookies)
   return request({
      url:'/comment',
      params:{
         t,
         type,
         id,
         content,
         cookie:encode
      }
   })
} 

