        <a href="javascript:;" @click="toHome"></a>
      </div>
      <ul class="nav-item clearfix">
        <router-link
          tag="li"
          active-class="active"
          v-for="(item, index) in nav"
          :key="index"
          class="nav-items"
          :to="item.path"
          :data-index="index"
          exact
        >
          {{ item.title }}
        </router-link>
        <!-- <li
          v-for="(item, index) in nav"
          :key="index"
          class="nav-items"
          @click="handleToPath(index, item.path)"
          :data-index="index"
          :class="{ active: index === curIndex }"
        >
          {{ item.title }}
        </li> -->
      </ul>
      <div class="nav-tools">
        <!--  -->
        <div class="serch">
          <input
            :class="{ actives: flag }"
            type="text"
            v-model="info"
            @focus="infoKong"
            @blur="infoYou"
            @input="getSearchSugGest"
            @keydown.enter="toSearch"
          />
          <tips
            v-if="searchTips"
            :searchList="searchList"
            :info="info"
            :class="{ tips: !searchTips }"
          ></tips>
        </div>
        <!--  -->
        <a class="creator" href="javascript:;"></a>
        <!--  Vuex-->
        <div class="login" @click="handleToLogin" v-if="headImg === false">
          <a href="javascript:;"></a>
        </div>
        <!--  -->
        <div
          class="head-portrait"
          v-else
          @mouseenter="listIsShow = true"
          @mouseleave="listIsShow = false"
        >
          <!--  -->
          <div class="photo">
            <img :src="headImg" alt="" />
            <!-- <i>1</i> -->
          </div>
          <!-- , -->
          <ul v-show="listIsShow">
            <li>
              <a
                href="javascript:;"
                @click="handleToUserHome($store.state.profile.userId)"
                ></a
              >
            </li>
            <li>
              <a
                href="https://gitee.com/xu-qingkuaile/vue_music"
                target="_blank"
                >Git</a
              >
            </li>
            <li>
              <a href="javascript:;" @click="toLevel"></a>
            </li>
            <li><a href="javascript:;" @click="logOut"></a></li>
          </ul>
        </div>
      </div>

      <div id="loginBox" v-if="showLoginBox">
        <!--  -->
        <h3 @mousedown="mousD"><span @click="noneLoginBox"></span></h3>
        <!--  -->
        <div id="QR-code" v-if="showQgLogin">
          <!--  -->
          <div class="code-bg" v-if="codeState">
            <!--  -->
            <div class="bg"></div>
            <!--  -->
            <div class="code">
              <h3></h3>
              <div class="code-img">
                <img :src="qrimg" alt="" />
              </div>
              <p><span>APP</span></p>
            </div>
          </div>
          <!--  -->
          <div class="confirmed" v-else>
            <!-- + -->
            <div class="bg-title">
              <!--  -->
              <div class="confirmed-bg"></div>
              <!--  -->
              <p class="confirmed-title"></p>
            </div>
            <p class="queren"></p>
          </div>
          <!--  -->
          <button @click="otherLogin"></button>
        </div>

        <!--  -->
        <div
          class="phone-login"
          v-else-if="showQgLogin === false && showPhoneLogin"
        >
          <!--  -->
          <div class="phone-bg"></div>
          <!--  -->
          <button class="button btn-login" @click="ToLogin"></button>
          <!--  -->
          <button class="button btn-zhuce"></button>
          <br />
          <!--  -->
          <label for="tongyi" class="label">
            <input type="checkbox" id="tongyi" v-model="flagClause" />
            
            <a
              v-for="item in clause"
              :key="item.url"
              :href="item.url"
              target="_blank"
              >{{ item.title }}</a
            >
          </label>

          <!--  -->
          <div class="back-qr-code" @click="backQrBox"></div>
        </div>
        <!--  -->
        <div class="phone-login-text" v-else @keyup.enter="denglu">
          <div class="username">
            <input type="tel" placeholder="" v-model="phone" />
          </div>
          <div class="password">
            <input
              type="password"
              placeholder=""
              v-model="password"
            />
          </div>
          <button class="button btn-login" @click="denglu"></button>
          <span @click="showPhoneLogin = true">&lt;</span>
        </div>
      </div>
      <!--  -->
      <div class="login-zhezhao" v-if="showLoginBox"></div>
      <!--  -->
      <div id="toast" v-if="showToast">
        
      </div>
      <!--  -->
      <div id="toast2" v-if="showToast2"></div>
    </div>
  </div>
</template>

<script>
// 
import { getQrCodeKey, getQrCode, getQrCheck } from "../../api/qr-code";
// 
import { phoneLogin } from "../../api/login";
// 
import { outLogin, getUserAdminDetail } from "../../api/user";
// 
import { searchSugGest } from "../../api/search";
// tips
import Tips from "./children/Tips.vue";
export default {
  name: "NavBar",
  components: { Tips },
  data() {
    return {
      nav: [
        { title: "", path: "/home" },
        { title: "", path: "/my" },
        { title: "", path: "/friend" },
        { title: "", path: "/store" },
        { title: "", path: "/musician" },
        { title: "", path: "/download" },
      ],
      //  path 
      curIndex: 0,
      info: "///",
      // 
      flag: false,
      // 
      clause: [
        {
          title: "",
          url: "https://st.music.163.com/official-terms/service",
        },
        {
          title: "",
          url: "https://st.music.163.com/official-terms/privacy",
        },
        {
          title: "",
          url: "https://st.music.163.com/official-terms/children",
        },
      ],
      // 
      flagClause: false,
      // 
      showLoginBox: false,
      // 
      showToast: false,
      // 
      phone: "",
      password: "",
      // 
      showQgLogin: true,
      // 
      showPhoneLogin: false,
      // key
      unikey: "",
      //  base64
      qrimg: "",
      // 
      timer: null,
      // 
      codeState: true,
      // 
      showToast2: false,
      // 
      listIsShow: false,
      // 
      debounce: null,
      // 
      searchTips: false,
      // 
      searchList: null,
      // NavBar
      headImg: JSON.parse(
        localStorage.getItem("headimg")
          ? localStorage.getItem("headimg")
          : "false"
      ),
    };
  },
  created() {
    this.unikey = "";
    this.codeState = true;
    let cookies = localStorage.getItem("userInfo");
    if (cookies) {
      //  cookies 
      // (),
      this.getUserData();
    }
  },
  mounted() {
    //  BUG
    let self = this;
    // 
    this.$bus.$on("showLoginBox", function () {
      self.handleToLogin();
    });
    this.$bus.$on("upheadimg", function () {
      self.headImg = JSON.parse(
        localStorage.getItem("headimg")
          ? localStorage.getItem("headimg")
          : "false"
      );
    });
  },
  methods: {
    // 
    async getUserData() {
      // 
      let { data: uad } = await getUserAdminDetail();
      this.$store.commit("USERDETAIL", uad.profile);
      localStorage.setItem("headimg", JSON.stringify(uad.profile.avatarUrl));
    },
    handleToUserHome(id) {
      this.$router.push({
        path: "/user/home",
        query: {
          id,
        },
      });
    },
    // 
    infoKong() {
      if (this.info === "///") {
        this.info = "";
        this.flag = true;
      } else {
        // else 
        // 
        this.getSearchSugGest();
      }
    },
    // 
    infoYou() {
      // tips()
      window.setTimeout(() => {
        this.searchTips = false;
      }, 100);
      //  
      if (!this.info) {
        // 
        this.info = "///";
        // 
        this.flag = false;
      } else {
        // 
        this.info = this.info;
      }
    },
    // 
    getSearchSugGest() {
      let self = this;
      if (this.debounce) {
        clearTimeout(this.debounce);
      }
      this.debounce = window.setTimeout(() => {
        if (self.info === "") {
          return;
        }
        if (self.info === "///") {
          return;
        }
        searchSugGest(self.info).then((v) => {
          if (v.data.code === 200) {
            self.searchList = v.data.result;
            self.searchTips = true;
          }
        });
        this.debounce = null;
      }, 500);
    },
    // 
    getQr() {
      let self = this;
      // 
      getQrCodeKey().then((v) => {
        let key = v.data.data.unikey;
        getQrCode(key).then((qrimg) => {
          // 
          this.qrimg = qrimg.data.data.qrimg;
          clearInterval(self.timer);
          // 
          self.timer = window.setInterval(() => {
            let date = new Date();
            getQrCheck(key, date).then((state) => {
              if (state.data.code === 800) {
                self.getQr();
              } else if (state.data.code === 802) {
                self.codeState = false;
              } else if (state.data.code === 803) {
                // cookie
                localStorage.setItem("userInfo", state.data.cookie);
                //  cookie 
                self.$bus.$emit("login");
                // 803
                clearInterval(self.timer);
                // 
                self.noneLoginBox();
                this.$store.commit("SHOW", true);
              }
            });
          }, 1000);
        });
      });
    },
    // 
    toastShow() {
      let self = this;
      this.showToast = true;
      window.setTimeout(() => {
        self.showToast = false;
      }, 1000);
    },
    // 
    toastShow2() {
      let self = this;
      this.showToast2 = true;
      window.setTimeout(() => {
        self.showToast2 = false;
      }, 1000);
    },
    // 
    mousD(e) {
      let OloginBox = document.querySelector("#loginBox");
      let x = e.pageX - OloginBox.offsetLeft; //
      let y = e.pageY - OloginBox.offsetTop; //
      document.onmousemove = function (event) {
        OloginBox.style.left = event.pageX - x + "px";
        OloginBox.style.top = event.pageY - y + "px";
      };
      document.onmouseup = function () {
        document.onmousemove = null;
      };
    },
    // 
    handleToLogin() {
      this.getQr();
      this.showLoginBox = true;
    },
    // 
    noneLoginBox() {
      this.showLoginBox = false;
      // 
      clearInterval(this.timer);
      this.showPhoneLogin = false;
      this.showQgLogin = true;
      this.flagClause = false;
      this.unikey = "";
      this.codeState = true;
    },
    // 
    ToLogin() {
      // 
      if (this.flagClause) {
        // 
        this.showPhoneLogin = false;
      } else {
        // 
        this.toastShow();
      }
    },
    // 
    async denglu() {
      let phone = this.phone;
      let password = this.password;
      // 
      let { data: res } = await phoneLogin(phone, password);
      if (res.code === 200) {
        this.noneLoginBox();
        //  token  cookie 
        localStorage.setItem("userInfo", res.cookie);
        localStorage.setItem("userToken", res.token);
        // 
        this.$bus.$emit("login");
        //  Vuex 
        this.$store.commit("SHOW", false);
      } else {
        this.toastShow2();
      }
    },
    // 
    otherLogin() {
      this.showQgLogin = false;
      this.showPhoneLogin = true;
      clearInterval(this.timer);
    },
    // 
    backQrBox() {
      if (this.flagClause) {
        this.showPhoneLogin = false;
        this.showQgLogin = true;
        this.getQr();
      } else {
        // 
        this.toastShow();
      }
    },
    // 
    logOut() {
      outLogin().then(async (v) => {
        if (v.data.code === 200) {
          localStorage.removeItem("headimg");
          localStorage.removeItem("userInfo");
          this.$store.commit("SHOW", true);
          this.$store.commit("USERDETAIL", null);
          this.$store.commit("USERLEVEL", null);
          this.$store.commit("USERINFO", null);
          if (this.$route.path === "/home") {
            this.$router.replace({
              path: "/home",
              query: {
                t: Date.now(),
              },
            });
          } else {
            this.$router.push("/home");
          }

          this.$bus.$emit("upheadimg");
          this.$bus.$emit("ShiFouDengLu", false);
        }
      });
    },
    // LOGO
    toHome() {
      if (this.$route.path === "/home") {
        return;
      } else {
        this.$router.push("/home");
      }
    },
    // 
    toSearch() {
      if (this.info === "") {
        return;
      } else {
        document.querySelector("input").blur();
        clearTimeout(this.debounce);
        this.$router.push({
          path: "/search",
          query: {
            s: this.info,
          },
        });
      }
    },
    // 
    toLevel() {
      this.$router.push({
        path: "/level",
      });
    },
  },
};
</script>

